import styles from "./Menu.module.css";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faStar,
  faLink,
  faWalking,
  faMapMarkerAlt,
} from "@fortawesome/free-solid-svg-icons";

function Menu({ id, mname, rate, name, cate, link, loc, distance }) {
  return (
    <div className={styles.gridContainer}>
      <div className={styles.mname}>
        <b>{mname}</b>
      </div>
      <div className={styles.empty}></div>
      <div className={styles.namecate}>
        <Link to={`/find/store/${id}`}>
          {name}({cate})
        </Link>
      </div>
      <div className={styles.rate}>
        <FontAwesomeIcon icon={faStar} /> {rate ? rate.toFixed(1) : "-"}
      </div>
      <div className={styles.link}>
        <a href={link} rel="noopener noreferrer" target="_blank">
          <FontAwesomeIcon icon={faLink} />
          <b> 링크</b>
        </a>
      </div>
      <div className={styles.loc}>
        <FontAwesomeIcon icon={faMapMarkerAlt} /> {loc}
      </div>
      <div className={styles.dist}>
        <FontAwesomeIcon icon={faWalking} /> 약 {distance}분
      </div>
    </div>
  );
}

export default Menu;
